import * as core from "@actions/core";
export class Logger {
    debugEnabled;
    constructor() {
        this.debugEnabled = core.isDebug();
    }
    info(message, data) {
        if (data !== undefined) {
            core.info(`${message} ${this.stringify(data)}`);
        }
        else {
            core.info(message);
        }
    }
    debug(message, data) {
        if (this.debugEnabled) {
            if (data !== undefined) {
                core.debug(`${message} ${this.stringify(data)}`);
            }
            else {
                core.debug(message);
            }
        }
    }
    error(message, error) {
        if (error instanceof Error) {
            core.error(`${message}: ${error.message}`);
            if (this.debugEnabled && error.stack) {
                core.debug(`Stack trace: ${error.stack}`);
            }
        }
        else if (error !== undefined) {
            core.error(`${message}: ${this.stringify(error)}`);
        }
        else {
            core.error(message);
        }
    }
    warning(message, data) {
        if (data !== undefined) {
            core.warning(`${message} ${this.stringify(data)}`);
        }
        else {
            core.warning(message);
        }
    }
    stringify(data) {
        try {
            return typeof data === "string" ? data : JSON.stringify(data, null, 2);
        }
        catch {
            return String(data);
        }
    }
}
